    Einzelfall speziellere Rechtsgrundlagen maßgeblich sein, teilen wir Ihnen
    diese in der Datenschutzerklärung mit.
  </p>
  <ul>
    <li>
      <strong
        >Vertragserfüllung und vorvertragliche Anfragen (Art. 6 Abs. 1 S. 1 lit.
        b. DSGVO)</strong
      > - Die Verarbeitung ist für die Erfüllung eines Vertrags, dessen Vertragspartei
      die betroffene Person ist, oder zur Durchführung vorvertraglicher Maßnahmen
      erforderlich, die auf Anfrage der betroffenen Person erfolgen.
    </li>
    <li>
      <strong
        >Rechtliche Verpflichtung (Art. 6 Abs. 1 S. 1 lit. c. DSGVO)</strong
      > - Die Verarbeitung ist zur Erfüllung einer rechtlichen Verpflichtung erforderlich,
      der der Verantwortliche unterliegt.
    </li>
    <li>
      <strong>Berechtigte Interessen (Art. 6 Abs. 1 S. 1 lit. f. DSGVO)</strong>
      - Die Verarbeitung ist zur Wahrung der berechtigten Interessen des Verantwortlichen
      oder eines Dritten erforderlich, sofern nicht die Interessen oder Grundrechte
      und Grundfreiheiten der betroffenen Person, die den Schutz personenbezogener
